.newpost {
    border-radius: 15px;
    width: 80%;
    height: 10vh;
    font-family: Roboto, Verdana, Tahoma, sans;
    text-align: center;
    font-size: 150%;
    margin-left: 10%;
    resize: none;
    min-height: 75px;
    overflow: hidden;
}

.posting {
    transform: translate(200%);
    border: none;
    outline: none;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    font-size: 15px;
    font-family: Roboto, Verdana, Tahoma, sans;
    padding: 5px 10px;
    border-radius: 10px;
    margin: 5px;
    background-color: white;
    height: 35px;
    width: 20%;

}

.posting:hover {
    transition: all .3s linear;
    -webkit-transition: all .3s linear;
    -moz-transition: all .3s linear;
    background: aliceblue;

}

.newpost {
    background-color: white;
}

