.AboutMeBlock {
    button {
        height: 30px;
    }
    button:hover {
        color: black;
        transition: all .3s linear;
        -webkit-transition: all .3s linear;
        -moz-transition: all .3s linear;
        background: aliceblue;
    }
}

.aboutMeButton {
    width: 100px;
}

.contacts {
    button {
        float: left;
        width: 100px;
    }
}

.closer {

}

.aboutMeContainer {
    position: fixed;
    top: -15px;
    left: -15px;
    width: 100%;
    height: 100%;
    z-index: 99999999;
    background: rgba(1, 1, 1, 0.7);
    overflow: hidden;
    white-space: nowrap;
    display: flex;
    align-items: center;
    justify-content: center;
    -ms-overflow-style: none;
}

.aboutMe{
    height: 600px;
    width: 700px;
    border-radius: 10px;
    box-shadow: black;
    position: fixed;
    margin: 0 auto;
    border: black 1px solid;
    background-color: white;
    color: black;
    overflow: hidden;
    button {
        float: right;
        width: 30px;
        height: 30px;
    }
}




.closer {
    width: 20px;
    height: 20px;
}

