.logs img {
    border-radius: 50px;
    width: 100%;
    min-height: 100%;
    margin-left: 10px;
    display: flex;
}

.UsersPhotoBlock {
    width: 9%;
    height: 9%;
    display: inline-block;
    vertical-align: 100%;
}

.logs {
    border-radius: 15px;
    width: 100%;
    position: relative;
    max-height: 400%;
}

.container {
    display: inline-block;
    font-size: 18px;
    position: relative;
}

.name {
    vertical-align: top;
    font-weight: bold;
    font-size: 20px;
}

@media (max-width: 1540px) {
}

.NameBlock {
    border: none;
    outline: none;
    color: black;
    text-decoration: none;
    display: inline-block;
    font-size: 25px;
    width: 100%;
}

.logs:hover {
    transition: all .3s linear;
    -webkit-transition: all .3s linear;
    -moz-transition: all .3s linear;
    background: aliceblue;
}

.ChangesBlock {
    display: flex;
    float: right;
    button {
        margin-top: 10px;
    }
    height: 40px;
    max-width: 100%;
}

button {
    margin-left: 5px;
    border: none;
    outline: none;
    font-family: Roboto, Verdana, Tahoma, sans;
    background: 0;
    font-size: 15px;
    border-radius: 10px;
    width: 80px;
}
button:hover {
    transition: all .3s linear;
    -webkit-transition: all .3s linear;
    -moz-transition: all .3s linear;
    background: white;
}

.commentMessage {
    display: block;
    max-width: 53vw;
    overflow-wrap: break-word;
    position: sticky;
}